.container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 30px 10px;
    background: #fff;

    > h3{
        font-weight: 500;
        font-size: 22px;
        margin: 0;
        color: #166987;
    }

    .block{
        margin: 30px 0 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        .image{
            width: 100%;
            padding-top: 50%;
            border-radius: 4px;
            margin: 0 0 12px;
            box-shadow: 0 0 7px #c7e1e9;
            background-position: center;
            background-size: cover;
            position: relative;
        }

        .label{
            font-weight: 500;
            color: #222222;
            font-size: 18px;
            margin: 0 0 4px;
            text-align: center;
            line-height: 28px;
        }

        .desc{
            font-size: 16px;
            line-height: 26px;
            text-align: center;
            padding: 0 4px;

            > span{
                font-weight: 500;
                color: #166987;
            }

            .name{
                font-size: 17px;
            }
        }
    }
}