.container {
    position: relative;
    padding-top: 60%;
    overflow: hidden;

    .nav {
        position: absolute;
        left: 0;
        top: 0;
        width: 100vw;
        height: 100%;
        z-index: 1;

        button {
            position: absolute;
            background: #ffffff;
            top: 50%;
            transform: translateY(-50%);
            border-radius: 3px;
            padding: 0;
            width: 24px;
            height: 24px;
            left: 6px;
            box-shadow: 0 0 4px #c7e1e9;

            &::before,
            &::after {
                content: "";
                position: absolute;
                width: 30%;
                height: 2px;
                background: #166987;
                left: 50%;
                top: 50%;
                transform-origin: 0 50%;
            }

            &::before {
                transform: translate(-50%, -50%) rotateZ(-36deg);
            }

            &::after {
                transform: translate(-50%, -50%) rotateZ(36deg);
            }

            &:nth-child(2) {
                right: 6px;
                left: auto;

                &::before,
                &::after {
                    transform-origin: 100% 50%;
                }
            }
        }
    }

    .items {
        position: absolute;
        z-index: 0;
        left: 0;
        top: 0;
        height: 100%;
        width: 500%;
        display: flex;
        align-items: center;
        justify-content: center;

        &.hasTransition {
            transition: all linear 0.2s;
        }
    }

    .item {
        position: relative;
        width: 20%;
        height: 100%;
        background-position: center;
        background-size: cover;
    }
}
